<template>
 <div>
  <div id="info">
   <br /><br />
   <span class="colorPicker">
    <input id="body-color" type="color" value="#ff0000" />
    <br />
    车身
   </span>
   <span class="colorPicker">
    <input id="details-color" type="color" value="#ffffff" />
    <br />
    轮毂
   </span>
   <span class="colorPicker">
    <input id="glass-color" type="color" value="#ffffff" />
    <br />
    玻璃
   </span>
  </div>

  <div id="container"></div>
 </div>
</template>

<script setup>
import { onMounted } from 'vue';
import * as THREE from 'three';
import { GLTFLoader } from 'three/addons/loaders/GLTFLoader.js';
import { DRACOLoader } from 'three/addons/loaders/DRACOLoader.js';
// 导入轨道控制器OrbitControls
import { OrbitControls } from 'three/addons/controls/OrbitControls.js'
import { RGBELoader } from 'three/addons/loaders/RGBELoader.js'

let camera, scene, renderer;

// 定义控制器对象
let controls, grid;

// 定义一个数组用于存储车轮模型对象
const wheels = []

// 车辆设置
// 加载车辆阴影贴图
const shadow = new THREE.TextureLoader().load(
 require('@/assets/ferrari_ao.png')
)

function init() {
  const container = document.getElementById('container');
  scene = new THREE.Scene();
  scene.background = new THREE.Color(0x333333);

  camera = new THREE.PerspectiveCamera(
    40,
    window.innerWidth / window.innerHeight,
    0.1,
    100
  );
  // 设置相机位置
  camera.position.set(4.25, 1.4, -4.5)

  renderer = new THREE.WebGLRenderer({ antialias: true });
  renderer.setSize(window.innerWidth, window.innerHeight);
  renderer.setAnimationLoop(render);
  container.appendChild(renderer.domElement);

 controls = new OrbitControls(camera, container);
   // 启用阻尼效果
 controls.enableDamping = true
 // 设置控制器能够缩放的最大距离
 controls.maxDistance = 9
 // 设置控制器的焦点位置
 controls.target.set(0, 0.5, 0)
 // 更新控制器
 controls.update()

 // 加载HDR贴图作为场景的环境贴图
 scene.environment = new RGBELoader().load(
  require('@/assets/venice_sunset_1k.hdr').default
 )
 // 设置环境贴图的映射方式为球形映射
 scene.environment.mapping = THREE.EquirectangularReflectionMapping

// 材料设置
// 创建车身材质对象，设置颜色和物理属性
const bodyMaterial = new THREE.MeshPhysicalMaterial({
    color: 0xff0000,
    metalness: 1.0,
    roughness: 0.5,
    clearcoat: 1.0,
    clearcoatRoughness: 0.03,
    sheen: 0.5
})

// 创建细节材质对象，设置颜色和物理属性
const detailsMaterial = new THREE.MeshStandardMaterial({
    color: 0xffffff,
    metalness: 1.0,
    roughness: 0.5
})

// 创建玻璃材质对象，设置颜色和物理属性
const glassMaterial = new THREE.MeshPhysicalMaterial({
    color: 0xffffff,
    metalness: 0.25,
    roughness: 0,
    transmission: 1.0
})

 // 获取车身颜色输入框元素
 const bodyColorInput = document.getElementById('body-color')
 // 监听车身颜色输入框的变化，设置车身材质的颜色
 bodyColorInput.addEventListener('input', function () {
  bodyMaterial.color.set(this.value)
 })

 // 获取细节颜色输入框元素
 const detailsColorInput = document.getElementById('details-color')
 // 监听细节颜色输入框的变化，设置细节材质的颜色
 detailsColorInput.addEventListener('input', function () {
  detailsMaterial.color.set(this.value)
 })

 // 获取玻璃颜色输入框元素
 const glassColorInput = document.getElementById('glass-color')
 // 监听玻璃颜色输入框的变化，设置玻璃材质的颜色
 glassColorInput.addEventListener('input', function () {
  glassMaterial.color.set(this.value)
 })

  const dracoLoader = new DRACOLoader();
  dracoLoader.setDecoderPath('decoder/');

  const loader = new GLTFLoader();
  loader.setDRACOLoader(dracoLoader);

// 加载 3D 文件
loader.load(require('@/assets/ferrari.glb').default, function (gltf) {
    // 获取加载的模型对象
    const carModel = gltf.scene.children[0]

    // 设置车身部分的材质为车身材质
    carModel.getObjectByName('body').material = bodyMaterial

    // rim_xx 参考 glb 文件配置
    // 设置前左轮辋的材质为细节材质
    carModel.getObjectByName('rim_fl').material = detailsMaterial
    // 设置前右轮辋的材质为细节材质
    carModel.getObjectByName('rim_fr').material = detailsMaterial
    // 设置后右轮辋的材质为细节材质
    carModel.getObjectByName('rim_rr').material = detailsMaterial
    // 设置后左轮辋的材质为细节材质
    carModel.getObjectByName('rim_rl').material = detailsMaterial
    // 设置车辆细节部分的材质为细节材质
    carModel.getObjectByName('trim').material = detailsMaterial
    // 设置玻璃部分的材质为玻璃材质
    carModel.getObjectByName('glass').material = glassMaterial
    // 将车辆的四个车轮模型对象添加到数组中
    wheels.push(
     carModel.getObjectByName('wheel_fl'),
     carModel.getObjectByName('wheel_fr'),
     carModel.getObjectByName('wheel_rl'),
     carModel.getObjectByName('wheel_rr')
    )

    // 阴影配置
    // 创建阴影平面模型
    const mesh = new THREE.Mesh(
     new THREE.PlaneGeometry(0.655 * 4, 1.3 * 4),
     new THREE.MeshBasicMaterial({
      map: shadow,
        // 混合
      blending: THREE.MultiplyBlending,
        // 映射
      toneMapped: false,
        // 透明
      transparent: true
     })
    )
    // 设置阴影平面模型的旋转角度
    mesh.rotation.x = -Math.PI / 2
    // 设置阴影平面模型的渲染顺序
    mesh.renderOrder = 2
    // 将阴影平面模型添加到车辆模型中
    carModel.add(mesh)

    // 将车辆模型添加到场景中
    scene.add(carModel)
})

  // 创建网格帮助器对象，设置网格的大小和颜色
 grid = new THREE.GridHelper(20, 40, 0xffffff, 0xffffff)
 // 设置网格材质的透明度
 grid.material.opacity = 0.2
 // 禁用网格材质的深度写入
 grid.material.depthWrite = false
 // 设置网格材质为透明材质
 grid.material.transparent = true
 // 将网格帮助器添加到场景中
 scene.add(grid)
}
function render() {
  controls.update();
  // 获取当前时间：https://developer.mozilla.org/zh-CN/docs/Web/API/Performance/now
  const time = -performance.now() / 1000
  // 设置网格的位置
  grid.position.z = -time % 1
  // 设置车轮的旋转角度
  for (let i = 0; i < wheels.length; i++) {
   wheels[i].rotation.x = time * Math.PI * 2
  }
  renderer.render(scene, camera);
  
}
onMounted(() => {
  init();
});
</script>
<style lang="scss">
body {
 color: #bbbbbb;
 background: #333333;
 text-align: center;
 overflow: hidden;
}

a {
 color: #08f;
}
.colorPicker {
 display: inline-block;
 margin: 0 10px;
}
</style>